.navbarMainContainer {
    display: flex;
    flex-direction: row;
    width: 90%;
    height: auto;
}

.logoContainer img  {
    width: auto;
    height: 4rem;
}

.linksContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    justify-content: space-between;
    align-items: baseline;
    padding-left: 100px;
    padding-right: 100px;
}

.initSessionContainer {
    display: flex;
    flex-direction: row;
    width: 25%;
    height: 100%;
    justify-content: center;
    align-items:center;
}

.initSessionBtn {
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items:center;
    width: auto;
    height:2.5em;
    background-color: var(--azul);
    color: var(--blanco);
    border-style: hidden;
    border-radius: 20px;
    padding-left: 1em;
    padding-right: 1em;
    grid-gap: 5px;
    gap: 5px;
    z-index: 2;
}

.sessionIcon {
    display: flex;
    flex-direction: column;
    width: auto;
    height:auto;
}

.initSessionBtn:hover {
    color: var(--azul);
    background-color: var(--verde);
}

.createUserBtn {
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:flex-start;
    width: 90px;
    height:2.5em;
    color: var(--azul);
    border-style: solid;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    padding-right: 0.5em;
    margin-left: -20px;
    z-index: 1;
}

.createUserBtn:hover {
    color: var(--verde);
}

.createUserBtn p {
    text-align: left;
    line-height: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 25px;
    padding: 0;
}

.sessionIndicator {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 70%;
    height:2.5em;
    color: var(--azul);
    border-style: solid;
    border-width: 2px;
    border-color: var(--azul);
    border-radius: 20px;
    padding-left: 1em;
    padding-right: 1em;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    background-color: var(--verde);
}

.sessionIndicator:hover {
    background-color:var(--azul);
    color: var(--blanco);
}
@media (max-width: 575.98px) {

    .navbarMainContainer {
        flex-direction: column;
        grid-gap: 10px;
        gap: 10px;
    }

    .container-fluid {
        flex-direction: column;
    }

    
    .linksContainer {
        flex-direction: column;
        justify-content: flex-start;
    }

    .initSessionContainer {
        width: 60%;
    }
    
}
