/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 120px;
  height: 45px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--grisDocSuave);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 36px;
  width: 36px;
  left: 4px;
  bottom: 3px;
  background-color: var(--azul);
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--grisDocSuave);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(73px);
  transform: translateX(73px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
  border-color: var(--azul);
  border-style: solid;
  border-width: 2px;
}

.slider.round:before {
  border-radius: 50%;
}

