  --grisLight: rgba(175, 175, 175, 0.05);
  --grisSuave: rgba(175, 175, 175, 1);
  --grisMedio: rgba(95, 95, 95, 1);
  --grisDoc: rgba(101, 110, 131, 0.7);
  --grisDocSuave: rgba(246, 249, 254, 1);
  --naranjo: rgba(252, 125, 74, 1);
  --amarillo: rgba(222, 201, 26, 1);
  --rosa: rgba(227, 99, 199, 1);
  --error: #e4032d;
  --ok: #009d4e;
  --Title: 600;
  --Link: 600;
  --Cuerpo: 400;
  --h1: 2.488rem;
  --h2: 2.074rem;
  --h3: 1.728rem;
  --h4: 1.44rem;
  --h5: 1.2rem;
  --notification: 0.833rem;
  --p: 14px;
  --bold: 700;
  --italic: italic;
  --docWidth: 80%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(0, 0, 0, 1);
  color: var(--negro);
  font-family: "Poppins", sans-serif;
  background-color: rgba(175, 175, 175, 0.05);
  background-color: var(--grisLight);
}

a {
  text-decoration: none;
  font-size: 14px;
  font-size: var(--p);
  font-weight: 600;
  font-weight: var(--Link);
}

a:hover {
  color: rgba(76, 15, 255, 1);
  color: var(--azul);
}

.animLink:after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: rgba(76, 15, 255, 1);
  background-color: var(--azul);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.animLink:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

.animLink {
  display: inline-block;
  position: relative;
  color: rgba(76, 15, 255, 1);
  color: var(--azul);
  text-decoration: none;
}

h1 {
  font-size: 2.488rem;
  font-size: var(--h1);
  font-weight: 600;
  font-weight: var(--Title);
}

h2 {
  font-size: 2.074rem;
