  font-weight: 600;
  font-weight: var(--Title);
}

h4 {
  font-size: 1.44rem;
  font-size: var(--h4);
  font-weight: 600;
  font-weight: var(--Title);
}

h5 {
  font-size: 1.2rem;
  font-size: var(--h5);
  font-weight: 600;
  font-weight: var(--Title);
}

p {
  font-size: 14px;
  font-size: var(--p);
  font-weight: 400;
  font-weight: var(--Cuerpo);
}

